.photo-upload {
    width: 128px;
    height: 128px;
}

.modal-action {
    user-select: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background-color: rgba(10, 10, 10, 0.2);
    border: 1px solid $white;
    border-radius: 290486px;
    cursor: pointer;
    display: inline-block;
    -webkit-box-flex: 0;
    -ms-flex-positive: 0;
    flex-grow: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    outline: none;
    position: relative;
    vertical-align: bottom;
    text-align: center;
    background: none;
    position: fixed;
    right: 20px;
    bottom: 20px;
    height: 32px;
    max-height: 32px;
    max-width: 32px;
    min-height: 32px;
    min-width: 32px;
    width: 32px;
    color: $white;

    .icon {
        position: absolute;
        right: 3px;
        bottom: 4px;
    }
}



